<template>
    <div class="queue">
        <h3 class="queue_title">目前队列还剩：{{waitperson}}</h3>
        <div class="queue_nextperson" @click="addNextperson">接待下一位</div>
    </div>
</template>

<script>
    import {
        mapMutations
    } from 'vuex'
    export default {
        data() {
            return {
                waitperson: 0
            }
        },
        methods: {
            ...mapMutations(['addNextPerson']),
            addNextperson() {
                let newperson = {
                    img: require('../assets/active.png'),
                    personname: '新人',
                    messagecount: 1
                }
                this.addNextPerson(newperson)
            }
        }
    }
</script>

