<template>
    <div class="main" id="main" @click="go">
        <Leftbar></Leftbar>
        <Left></Left>
        <keep-alive>
            <Set v-if="isset"></Set>
            <Mid v-else></Mid>
        </keep-alive>
        <Right></Right>
    </div>
</template>

<script>
    import Left from '../components/left'
    import Mid from '../components/mid'
    import Right from '../components/right'
    import Leftbar from '../components/leftbar'
    import Set from '../components/set'
    import {
        mapState,
        mapMutations
    } from 'vuex';
    export default {
        components: {
            Leftbar,
            Left,
            Mid,
            Right,
            Set
        },
        computed: {
            ...mapState(['password', 'Loginstate', 'isset'])
        },
        methods: {
            ...mapMutations(['loginStateDelete']),
            go() {
                if (!this.Loginstate) {
                    this.$Message.warning('登陆状态过期，请重新登陆')
                    setTimeout(() => {
                        this.$router.replace('/login')
                    }, 800)
                }
            },
            clearModule() {
                let Cookieparse = JSON.parse(document.cookie);
                Cookieparse.userpassword = "";
                this.loginStateDelete();
            },
            loginStateTest() {
                setInterval(() => {
                    let Cookieparse = JSON.parse(document.cookie);
                    if (new Date().getTime() - Cookieparse.logintime >=  60 * 60 * 1000) {
                        this.clearModule();
                    }
                }, 5000);
            }
        },
        created() {
            this.loginStateTest()
        }
    }
</script>

<style>
    .main {
        height: 92vh;
    }
</style>
