<template>
    <div class="leftbar">
        <Tooltip content="设置" placement="right" class="leftbar_setbox" theme='light'>
            <img :src="ishover?y:n" @click="changeSet" @mouseover="mousein" @mouseout="mouseout" alt="" srcset="" class="leftbar_set">
        </Tooltip>
    </div>
</template>

<script>
    import {
        mapMutations
    } from 'vuex';
    export default {
        data() {
            return {
                ishover: false,
                isset: false,
                y: require('../assets/setactive.png'),
                n: require('../assets/setnormal.png'),
            }
        },
        methods: {
            ...mapMutations(['isSetTrue']),
            mousein() {
                this.ishover = true
            },
            mouseout() {
                this.ishover = false
            },
            changeSet() {
                this.isSetTrue()
            }
        }
    }
</script>

<style lang="less" scoped>
    .leftbar {
        height: 92vh;
        width: 3%;
        float: left;
        background-color: #393e46;
        position: relative;
        .leftbar_setbox {
            position: absolute;
            bottom: 30px;
            left: 50%;
            transform: translate(-50%, 0px);
            .leftbar_set {
                width: 25px;
                height: 25px;
            }
        }
    }
</style>

