<template>
    <div class="chart_col chart_colleft">
        <Queue></Queue>
        <div class="colleft_list" :class="cur==index?s:h" @click="goChartList(item.personname)" @mouseenter='mousein(index)' @mouseleave='mouseout(index)' v-for="(item,index) in talkingpersonlist" :key="index">
            <div class="colleft_close" v-show="cur==index?t:f" @click.stop="closelist(index)">结束</div>
            <div class="demo-avatar-badge colleft_img">
                <Badge :count="item.messagecount">
                    <Avatar shape="square" :src="item.img" />
                </Badge>
            </div>
            <!-- <img :src="item.img" class="colleft_img"> -->
            <p class="colleft_p">{{item.personname}}</p>
        </div>
    </div>
</template>

<script>
    import Queue from './queue'
    import {
        mapMutations,
        mapState
    } from 'vuex';
    export default {
        data() {
            return {
                cur: 999,
                t: true,
                f: false,
                s: 'enter',
                h: 'leave',
                talkingpersonlist: [{
                    img: require('../assets/active.png'),
                    personname: 'asdasd',
                    messagecount: 4
                }, {
                    img: require('../assets/active.png'),
                    personname: '123123',
                    messagecount: 5
                }],
            }
        },
        components: {
            Queue
        },
        methods: {
            ...mapMutations(['talkingUserState', 'talkingUserStateClose']),
            goChartList(x) {
                this.talkingUserState(x)
            },
            closelist(x) {
                this.talkingpersonlist.splice(x, 1)
                this.talkingUserStateClose()
            },
            mousein(x) {
                this.cur = x
            },
            mouseout(x) {
                this.cur = 999
            }
        },
        computed: {
            ...mapState(['nextpersonmeg', 'clearPersonname'])
        },
        created() {},
        watch: {
            nextpersonmeg(x) {
                if (this.talkingpersonlist.length >= 5) {
                    this.$Message.warning('最多接待五位,勇士请继续加油！')
                } else {
                    this.talkingpersonlist.push(x)
                }
            },
            clearPersonname(x) {
                this.talkingpersonlist.find(item => item.personname == x).messagecount = 0
            }
        }
    }
</script>

